@import "../../Common/Variable/Variable.scss";

.main-banner-wrapper {
  width: 100%;
  height: 40rem;
  background-image: url(../../Assests/Banners/main-banner.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  transition: all 0.5s ease;

  .main-banner {
    height: 100%;
    display: flex;
    align-items: center;
    padding-left: 10%;

    .content {
      display: flex;
      flex-direction: column;
      gap: 1rem;
      padding-top: 3rem;
      color: white;
      h2 {
        font-size: 2.5rem;
      }
      h3 {
        font-size: 1.5rem;
      }
      a {
        background-color: $main;
        font-size: 1.2rem;
        font-weight: 500;
        padding: 1rem 1.5rem;
        align-self: flex-start;
        transition: all 0.5s ease;
        border-radius: 5px;

        &:hover {
          background-color: white;
          color: $main;
        }
      }
    }
  }
}

.aboutH {
  background: url(../../Assests/Banners/blue.png);
  background-size: cover;
  background-position: center center;
  display: flex;
  align-items: center;
  gap: 2rem;
  flex-wrap: wrap;
  padding: 5% 10%;

  .info {
    width: 48%;
    display: flex;
    flex-direction: column;
    gap: 1rem;

    h2 {
      font-size: 2.2rem;

      span {
        color: $main;
      }
    }

    p {
      text-align: justify;
    }

    a {
      background-color: $main;
      align-self: flex-start;
      padding: 1rem;
      color: white;
      border-radius: 5px;

      &:hover {
        background-color: white;
        color: $main;
        // box-shadow: 0px 0px 40px -20px rgba(0, 0, 0, 0.5);
      }
    }
  }

  .content {
    width: 48%;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    .content-container {
      padding: 1rem;
      background-color: #fff;
      display: flex;
      align-items: center;
      gap: 1rem;
      .icon {
        min-width: 4rem;
        svg {
          font-size: 3rem;
        }
      }
      .content-info {
        h3 {
          font-size: 1.5rem;
        }
        p {
          font-size: 1rem;
          line-height: 1.5rem;
        }
      }
    }
  }
}

.maintainH {
  display: flex;
  align-items: center;
  gap: 2rem;
  flex-wrap: wrap;
  padding: 5% 10%;

  .img {
    width: 48%;
  }

  .info {
    width: 48%;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    h2 {
      font-size: 2.2rem;
      line-height: 2.5rem;

      span {
        color: $main;
      }
    }
  }
}
